</template>

<script>
import {
  getProjectList,
  addProject,
  deleteProject,
  updateProject,
  getDetail
} from "../../api/project/index";
import { getCorporationCompany } from "../../api/company/index"
import { geTypeAll } from "../../api/file/data"
import selectCity from "../../components/selectCity.vue";
import { Promise, reject } from 'q';
import { resolve } from 'url';
const formJson = {
  username: '',
  password: '',
  name: '',
  type: '',
  structure_type: '',
  scale: '',
  layers: '',
  engineering_cost: '',
  starttime: '',
  endtime: '',
  license: '',
  tzscpz: '',
  construction_unit: '',
  construction_user: '',
  survey_unit: '',
  survey_user: '',
  design_unit: '',
  design_user: '',
  shigong_unit: '',
  shigong_user: '',
  supervision_unit: '',
  supervision_user: '',
  detection_unit: '',
  detection_user: '',
  monitoring_group: '',
  provinceid: '',
  cityid: '',
  areaid: '',
  address: ''
};
export default {
  components: {
    selectCity: selectCity
  },
  data() {
    return {
      roles: [],
      query: {
        keyword: '',
        page: 1,
        limit: 20,
      },
      list: [],
      total: 0,
      loading: true,
      index: null,
      formName: null,
      address: [620000, 620800, 620821],
      formMap: {
        add: "",
        edit: ""
      },
      options: [],
      projectType: [],
      sortList: [],
      formLoading: false,
      formVisible: false,
      formData: formJson,
      formRules: {},
      addRules: {
        username: [
          { required: true, message: "", trigger: "blur" }
        ],
        name: [
          { required: true, message: "", trigger: "blur" }
        ],
        type: [
          { required: true, message: "", trigger: "change" }
        ],
        structure_type: [
          { required: true, message: "", trigger: "change" }
        ],
        scale: [
          { required: true, message: "", trigger: "blur" }
        ],
        layers: [
          { required: true, message: "", trigger: "blur" }
        ],
        engineering_cost: [
          { required: true, message: "", trigger: "blur" }
        ],
        starttime: [
          { required: true, message: "", trigger: "change" }
        ],
        endtime: [
          { required: true, message: "", trigger: "change" }
        ],
        license: [
          { required: true, message: "", trigger: "blur" }
        ],
        tzscpz: [
          { required: true, message: "", trigger: "blur" }
        ],
        monitoring_group: [
          { required: true, message: "", trigger: "blur" }
        ],
        construction_unit: [
          { required: true, message: "", trigger: "change" }
        ],
        survey_unit: [
          { required: true, message: "", trigger: "change" }
        ],
        design_unit: [
          { required: true, message: "", trigger: "change" }
        ],
        shigong_unit: [
          { required: true, message: "", trigger: "change" }
        ],
        supervision_unit: [
          { required: true, message: "", trigger: "change" }
        ],
        detection_unit: [
          { required: true, message: "", trigger: "change" }
        ],
      },
      deleteLoading: false
    };
  },
  methods: {
    districtChange(val) {
      this.formData.provinceid = val[0]
      this.formData.cityid = val[1]
      this.formData.areaid = val[2]
    },
    getCompany() {
      let params = {
        keyword: ''
      }
      getCorporationCompany(params).then(res => {
        this.options = res || []
      })
    },
    getType(pid) {
      let params = {
        pid: pid,
        keyword: ''
      }
      return new Promise((resolve, reject) => {
        geTypeAll(params).then(res => {
          resolve(res)
        })
      })
    },
    viewDetail(index, row) {
      this.$router.push('projectDetail?id=' + row.id)
    },
    onReset() {
      this.$router.push({
        path: ""
      });
      this.query = {
        keyword: "",
        page: 1,
        limit: 20
      };
      this.getList();
    },
    onSubmit() {
      this.$router.push({
        path: "",
        query: this.query
      });
      this.getList();
    },
    handleCurrentChange(val) {
      this.query.page = val;
      this.getList();
    },
    getList() {
      this.loading = true;
      getProjectList(this.query)
        .then(response => {
          this.loading = false;
          this.list = response.data || [];
          this.total = response.count || 0;
        })
        .catch(() => {
          this.loading = false;
          this.list = [];
          this.total = 0;
          this.roles = [];
        });
    },
    getRoleList() {
      authAdminRoleList(this.query)
        .then(response => {
          this.roles = response.data.list || [];
        })
        .catch(() => {
          this.roles = [];
        });
    },
    // 
    resetForm() {
      if (this.$refs["dataForm"]) {
        // 
        this.$refs["dataForm"].clearValidate();
        // 
        this.$refs["dataForm"].resetFields();
      }
    },
    // 
    hideForm() {
      // 
      this.formVisible = !this.formVisible;
      // 
      this.$refs["dataForm"].resetFields();
      return true;
    },
    // 
    handleForm(index, row) {
      this.formVisible = true;
      this.formData = JSON.parse(JSON.stringify(formJson));
      if (row !== null) {
        this.getProjectDetail(row.id)
        // this.formData = Object.assign({}, row);
        // console.log(row)

        // this.formData.
      }
      this.formName = "add";
      this.formRules = this.addRules;
      if (index !== null) {
        this.index = index;
        this.formName = "edit";
        this.formRules = this.addRules;
      }
    },
    getProjectDetail(id) {
      getDetail({ id }).then(res => {
        this.formData = res || {}
        this.formData.design_user = Number(this.formData.design_user)
        this.formData.structure_type = Number(this.formData.structure_type)
        if (this.formData.provinceid) {
          this.address = [this.formData.provinceid, this.formData.cityid, this.formData.areaid]
        }
        console.log(this.formData)
      })
    },
    editProject(data) {
      updateProject(data).then(response => {
        if (response) {
          this.formLoading = false;
          this.$message.success("");
          this.formVisible = false;
          this.getList()
          // 
          this.resetForm();
        }
      });
    },
    formSubmit() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          if (!this.formData.provinceid) {
            this.$message.warning('')
          } else if (!this.formData.address) {
            this.$message.warning('')
          } else if (!this.formData.construction_user) {
            this.$message.warning('')
          } else if (!this.formData.survey_user) {
            this.$message.warning('')
          } else if (!this.formData.design_user) {
            this.$message.warning('')
          } else if (!this.formData.shigong_user) {
            this.$message.warning('')
          } else if (!this.formData.supervision_user) {
            this.$message.warning('')
          } else if (!this.formData.detection_user) {
            this.$message.warning('')
          } else {
            this.formLoading = true;
            let starttime = this.formData.starttime + ''
            this.formData.starttime = starttime.substring(0, 10)
            let endtime = this.formData.endtime + ''
            this.formData.endtime = endtime.substring(0, 10)
            let data = Object.assign({}, this.formData);
            if (this.formData.id) {
              this.editProject(data)
            } else {
              addProject(data).then(response => {
                if (response) {
                  this.formLoading = false;
                  this.$message.success("");
                  this.formVisible = false;
                  this.getList()
                  // 
                  this.resetForm();
                }
              });
            }
          }
        }
      });
    },
    // 
    handleDel(index, row) {
      if (row.id) {
        this.$confirm("?", "", {
          type: "warning"
        })
          .then(() => {
            let para = { id: row.id };
            this.deleteLoading = true;
            deleteProject(para)
              .then(response => {
                this.deleteLoading = false;
                if (response.code) {
                  this.$message.error(response.message);
                  return false;
                }
                this.$message.success("");
                // 
                this.list.splice(index, 1);
              })
              .catch(() => {
                this.deleteLoading = false;
              });
          })
          .catch(() => {
            this.$message.info("");
          });
      }
    }
  },
  created() {
    // 
    let query = this.$route.query;
    this.query = Object.assign(this.query, query);
    this.query.limit = parseInt(this.query.limit);
    // 
    this.getList();
    this.getCompany()
    this.getType(1).then(res => {
      this.projectType = res
    })
    this.getType(2).then(res => {
      this.sortList = res
    })
    this.formData.provinceid = 620000
    this.formData.cityid = 620800
    this.formData.areaid = 620821
  }
};
</script>

